<template>
  <a-card>
    <a-form-model labelAlign="left" >
      <div class="">
      <a-form-model-item label="">
        <a-input style="width: 300px" v-model="profileInfo.name"></a-input>
      </a-form-model-item>

      <a-form-model-item label="">
        <a-input type="textarea" v-model="profileInfo.desc"></a-input>
      </a-form-model-item>

      <a-form-model-item label="QQ">
        <a-input style="width: 300px" v-model="profileInfo.qq_chat"></a-input>
      </a-form-model-item>

      <a-form-model-item label="">
        <a-input style="width: 300px" v-model="profileInfo.wechat"></a-input>
      </a-form-model-item>

      <a-form-model-item label="">
        <a-input style="width: 300px" v-model="profileInfo.weibo"></a-input>
      </a-form-model-item>

      <a-form-model-item label="B">
        <a-input style="width: 300px" v-model="profileInfo.bili"></a-input>
      </a-form-model-item>

        <a-form-model-item label="Github">
          <a-input style="width: 300px" v-model="profileInfo.github"></a-input>
        </a-form-model-item>

      <a-form-model-item label="Email">
        <a-input style="width: 300px" v-model="profileInfo.email"></a-input>
      </a-form-model-item>
      </div>

      <a-form-model-item label="">
        <a-upload
          listType="picture"
          name="file"
          :action="upUrl"
          :headers="headers"
          @change="avatarChange"
        >
          <a-button style="margin-right:10px">
            <a-icon type="upload" />
          </a-button>

          <template v-if="profileInfo.avatar">
            <img :src="profileInfo.avatar" style="width: 120px; height: 100px" />
          </template>
        </a-upload>
      </a-form-model-item>

      <a-form-model-item label="">
        <v-md-editor
          v-model="profileInfo.aboutMe"
          :disabled-menus="[]"
          height="600px"
          @upload-image="handleUploadImage">

        </v-md-editor>
      </a-form-model-item>

      <a-form-model-item>
        <a-button type="danger" style="margin-right: 15px" @click="updateProfile"></a-button>
      </a-form-model-item>
    </a-form-model>
  </a-card>
</template>
<script>
import { Url } from '../../plugin/http'

export default {
  data() {
    return {
      profileInfo: {
        id: 1,
        name: '',
        desc: '',
        qq_chat: '',
        wechat: '',
        weibo: '',
        bili: '',
        github: '',
        email: '',
        avatar: '',
        aboutMe: '',
      },
      upUrl: Url + 'upload',
      headers: {},
    }
  },
  created() {
    this.getProfileInfo()
    this.headers = { Authorization: `Bearer ${window.sessionStorage.getItem('token')}` }
  },
  methods: {
    // 
    async getProfileInfo() {
      const { data: res } = await this.$http.get(`profile/${this.profileInfo.id}`)
      if (res.code !== 200) {
        if (res.code === 1004 || 1005 || 1006 || 1007) {
          window.sessionStorage.clear()
          this.$router.push('/login')
        }
        this.$message.error(res.message)
      }
      console.log(res.data)
      this.profileInfo = res.data
    },

    // 
    avatarChange(info) {
      if (info.file.status !== 'uploading') {
      }
      if (info.file.status === 'done') {
        this.$message.success(``)
        const imgUrl = info.file.response.url
        this.profileInfo.avatar = imgUrl
      } else if (info.file.status === 'error') {
        this.$message.error(``)
      }
    },

    async handleUploadImage(event, insertImage, files) {
      //  files 
      console.log(files);
      console.log(event)
      let form = new FormData();
      let file = event.target.files[0]
      form.append('file', file);
      let config = {
        headers: {'Content-Type': 'multipart/form-data'}
      }
      let {data:res}=await this.$http.post('upload', form,config)
      console.log(res)
      // 
      insertImage({
        url: res.url,
        desc: res.name,
        // width: 'auto',
        // height: 'auto',
      });
    },

    // 
    async updateProfile() {
      const { data: res } = await this.$http.put(`profile/1`, this.profileInfo)
      if (res.code !== 200) return this.$message.error(res.message)
      this.$message.success(``)
      this.$router.push('/index')
    },
  },
}
</script>

<style scoped>
.upBtn {
  margin-right: 10px;
}
</style>
